<template>
    <div>
            <!-- submit.prevent, prevents re-loading of webpage -->
            <form @submit.prevent="addTodo">
                <input type="text" v-model="title" name="title" placeholder="Add Todo...">
                <input type="submit" value="Submit" class="btn">
            </form>
    </div>
</template>

<script>
// uuid installed via npm i uuid, used to quickly add unique ID for NewToDos.
// to install uuid, use "npm i uuid"
// import uuid from 'uuid'; and using uuid.v4().
// t...his had a provblem, so trying this from comments of crash course
// and instead of uuid.v4();, use uuidv4() below.
import {v4 as uuidv4} from 'uuid';

export default {
    name: "AddTodo",
    data() {
        return {
            title: ''
        }
    },
    methods: {
        addTodo(e) {
            // dont' want to add to file.. 
            e.preventDefault();
            const newTodo = {
                id: uuidv4(),
                title: this.title,
                completed: false
            }
            // Send up to parent
            this.$emit('add-todo', newTodo);
            this.title = '';
        }
    }
}
</script>

<style scoped>
    /* Flex box to do quick formatting, read up on flex box */
    form {
        display: flex;
    }

    input[type="text"] {
        flex: 10;
        padding: 5px;
    }

    input[type="submit"] {
        flex: 2;
    }
</style>